.film {
  width: 400px;
  padding: 25px;
  color: #fff;
  position: relative;
}

.vote {
  color: #e1e4d9;
  background: #cf7a05;
  width: 50px;
  height: 50px;
  border-radius: 50px;
  line-height: 50px;
  font-weight: 900;
  box-shadow: 0px 0px 10px 5px rgb(40, 53, 52);
  position: absolute;
  top: 220px;
  right: 70px;
}

.button {
  height: 30px;
  background: transparent;
  width: 30px;
  border: none;
}

.watchedButton {
  height: 20px;
  width: 20px;
  fill: rgb(215, 215, 226);
}
